import { createStore } from "vuex";

export default createStore({
    actions:{

    },
    mutations:{
        addCart(state,value){
            state.cartList = value
        },

        // 是否显示编辑功能
        isShowEdit(state){
            state.isShowDelete = !state.isShowDelete
        },

        // 删除之后 购物车里所剩的商品
        delete(state,value){
            state.cartList = value
        },

        edit(state){
            if(state.cartList.length === 0){
                state.edit = true
            }else{
                state.edit = !state.edit
            }
        },

        // 生成订单逻辑
        pay(state,value){
            state.orderList = value
        },

        // 已完成的订单拼接
        orderListEnd(state){
            state.orderListEnd = state.orderListEnd.concat(state.orderList)
        },

        // 新增地址
        addAddress(state,value){
            // 用户是否将新增的地址设为默认，如果是则把其它地址改为非默认
            state.userAddress.map((item,index) => {
                if(value.isDefault){
                    item.isDefault = false
                }
            });
            value.id = state.userAddress[state.userAddress.length-1].id + 1
            state.userAddress.push(value)
        },
        // 编辑地址
        editAddress(state,value){
            state.userAddress = state.userAddress.map((item)=>{
                if(value.isDefault){
                    item.isDefault = false
                }
                if(item.id === value.id){
                    return value
                }else{
                    return item
                }
            })
        },
        // 删除地址
        deleteAddress(state,value){
            state.userAddress=state.userAddress.filter(item =>{
                return !(item.id === value.id)
            })
            if(value.isDefault){
                if(state.userAddress.length){
                    state.userAddress[0].isDefault = true
                }
            }
        }
    },
    state:{
        cartList:[], //购物车的数据
        isShowDelete:true, // Hearder组件的 编辑功能是否显示
        edit:true, // Header组件，true为编辑，false为完成
        orderList:[], //订单
        userAddress:[
            {
                id: 1001,
                name: 'jason',
                tel: '15874859686',
                province: '广东省',
                city: '深圳市',
                county: '南山区',
                addressDetail: '深圳湾',
                isDefault: true,
                areaCode: '110101',
              },
              {
                id: 1002,
                name: 'allen',
                tel: '15874859685',
                province: '湖北省',
                city: '武汉市',
                county: '武昌区',
                addressDetail: '黄鹤楼',
                isDefault: false,
                areaCode: '110101',
              },
        ], //用户信息
        orderListEnd:[] //完成的订单
    }
})