// import VueRouter from 'vue-router' // 导入路由
// import home from '../views/home' //导入首页视图
// import cart from '../views/cart' //导入购物车视图
// import order from '../views/order' //导入订单视图
// import mine from '../views/mine' //导入我的视图

import { showToast } from 'vant'
import { createRouter ,createWebHashHistory } from 'vue-router'
// 1. 定义 (路由) 组件。
// 2. 创建路径与路由的映射关系
// 3. 创建vueRouter实例并暴露出去


//定义一些路由
// 每个路由都需要映射到一个组件。
const router = createRouter({
    history:createWebHashHistory(),
    routes:[
        {path:'/',component:()=> import('../views/home')},
        {path:'/home',component:()=> import('../views/home')},
        {path:'/cart',component:()=> import('../views/cart'),meta:{isAuth:true}},
        {path:'/order',component:()=> import('../views/order'),meta:{isAuth:true}},
        {path:'/mine',component:()=> import('../views/mine'),meta:{isAuth:true}},
        {path:'/store',component:()=> import('../views/store')},
        {path:'/createOrder',component:()=> import('../views/createOrder'),meta:{isAuth:true}},
        {path:'/address',component:()=> import('../views/address'),meta:{isAuth:true}},
        {path:'/addressEdit',component:()=> import('../views/addressEdit'),meta:{isAuth:true}},
        {path:'/userinfoedit',component:()=> import('../views/userinfoedit'),meta:{isAuth:true}},
        {path:'/login',component:()=> import('../views/login')},
        {path:'/register',component:()=> import('../views/register')}
    ]
})
router.beforeEach((to,from,next) =>{
    if(to.meta.isAuth){
        // 登录之后，会在localstorage里面 存储可以标识，就可以访问全部页面了
        if(localStorage.isLogin === 'login'){
            next()
        }else{
            showToast('请先登录')
            setTimeout(() => {
                router.push('/login')
            }, 1000);
        }
    }else{
        next()
    }
})



export default router